
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import Drive from '@/tool/classFactory/drive.js';
import {getCookie} from '@/tool/common.js';
export default {
components: {
Group,
Cell,
XInput,
XButton,
Toast
},
data () {
return {
name:'',
msg: 'Hello World!',
sessionId:'',
params:{
'nickname':'',
'sessionId':''
},
showPositionValue: false,
msg: '请输入姓名',
middle:'middle'
}
},
created () {
this.params.sessionId = getCookie('sessionId')
console.log(this.params.sessionId)
},
methods: {
change (val, label) {
console.log('change', val, label)
},
onComfirmTap (e){
console.log(this.params)
if(this.params.nickname){
this.updateUserMsg()
}else {
this.showPositionValue = true
}

},
updateUserMsg (e){
const that = this;
Drive.prototype.updateUserMsg(that.params).then(res => {
let list = res.data.data || [];
that.$router.push('../detailmsg')
console.log(list)
});
}
}
}



